 <template>
  <form>
    <label for="uname">
      <b>Username</b>
    </label>
    <input
      type="text"
      v-model="state.email_or_username"
      placeholder="Enter Username"
      name="uname"
      required
    >

    <label for="psw">
      <b>Password</b>
    </label>
    <input
      type="password"
      v-model="state.password"
      placeholder="Enter Password"
      name="psw"
      required
    >

    <button @click="login" type="submit">Login</button>
  </form>
</template>
 <script>
import loginRepository from "@/repository/login";
export default {
  data() {
    return {
      state: {
        grant_type: "password",
        email_or_username: "",
        password: "",
        client_id: "web_plateform_client_id",
        user_agent: navigator.userAgent
      }
    };
  },
  methods: {
    async login() {
      await loginRepository.login(this.state);
    }
  }
};
</script>

